/* App.css */

body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Header */
#header {
  background-color: #1c1c1c;
  padding: 20px 0;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 1000;
}

#header h1 a {
  color: #ffffff;
  font-size: 32px;
  text-decoration: none;
}

#header h2 {
  color: #ffffff;
  font-size: 18px;
  margin-top: 4px;
}

.navbar ul {
  list-style: none;
  display: flex;
  justify-content: flex-end;
  margin: 0;
}

.navbar li {
  margin: 0 20px;
}

.navbar a {
  color: #ffffff;
  text-decoration: none;
  font-size: 18px;
  transition: 0.3s;
}

.navbar a:hover {
  color: #e44d26;
}

/* Sections */
.container {
  width: 80%;
  margin: 0 auto;
}

.section-title h2 {
  font-size: 36px;
  margin-bottom: 20px;
}

.section-title p {
  font-size: 18px;
  color: #777;
}

/* About Section */
.about {
  padding: 80px 0;
}

.about img {
  max-width: 100%;
  height: auto;
}

.about-me h3 {
  font-size: 28px;
  color: #e44d26;
}

.about-me p {
  font-size: 18px;
  color: #777;
}

/* Projects Section */
.projects {
  background-color: #f5f5f5;
  padding: 80px 0;
}

.icon-box {
  background-color: #ffffff;
  padding: 30px;
  text-align: center;
  border-radius: 8px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  transition: 0.3s;
  margin-bottom: 20px;
}

.icon-box:hover {
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.2);
}

.icon {
  font-size: 48px;
  color: #e44d26;
}

.icon-box h4 {
  font-size: 24px;
  color: #222;
  margin: 10px 0;
}

.icon-box p {
  font-size: 16px;
  color: #777;
}

/* Contact Section */
.contact {
  padding: 80px 0;
}

.info-box {
  background-color: #ffffff;
  padding: 30px;
  text-align: center;
  border-radius: 8px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  transition: 0.3s;
  margin-bottom: 20px;
}

.info-box:hover {
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.2);
}

.info-box i {
  font-size: 48px;
  color: #e44d26;
}

.info-box h3 {
  font-size: 24px;
  color: #222;
  margin: 10px 0;
}

.info-box p {
  font-size: 16px;
  color: #777;
}

/* Footer */
footer {
  background-color: #1c1c1c;
  color: #ffffff;
  text-align: center;
  padding: 20px 0;
  position: fixed;
  width: 100%;
  bottom: 0;
}
